import React, { useContext } from "react";

import { SymbolItemContext } from "../contexts/SymbolItemContext";
import { LayerItem } from "./LayerItem";
import { SymbolPropertyEditing } from "./SymbolPropertiesEditing";
import { CalciteButton } from "@esri/calcite-components-react";

export const LayersPanel = () => {
  const {
    renderTree,
    inGallery,
    changeInGallery,
  } = useContext(SymbolItemContext);

  const layersToggle = () => {
    changeInGallery(!inGallery);
  };

  return (
    <div className="layerPanel" key="layersPanel">
      {renderTree ? (
        <LayerItem
          treeNode={renderTree}
          key="layerItem"
          level={0}
          path={[]}
        />
      ) : null}
      {
        <CalciteButton className="addLayerBtn" scale="s" appearance="clear" onClick={layersToggle}>
          Add Symbol Layer
        </CalciteButton>
      }
      {<SymbolPropertyEditing />}
    </div>
  );
};
