import React, { useContext } from "react";
import { SymbolPreview } from "./SymbolPreview";
import { SymbolLayersEditor } from "./SymbolLayersEditor";
import { SymbolItemContext } from "../contexts/SymbolItemContext";
import { CalciteIcon, CalcitePopoverManager, CalcitePopover } from "@esri/calcite-components-react";

export const SymbolBuilder = () => {
  const { currentSymbolType, changeCurrentSymbolType } = useContext(SymbolItemContext);

  const clickHandler = (evt: React.MouseEvent)=>{
    const element = evt.target as HTMLButtonElement;
    changeCurrentSymbolType(element.dataset["type"])
  }
  return (
    <div className="symbolBuilder">
      <div className="symbolBuilder_Title">
        <CalcitePopoverManager autoClose>
        <CalcitePopover label="popover" referenceElement="popover-help">
          <div className="popoverContent" >
            <p><b>Need help?</b> Click the "Add Symbol Layer" button to get started. For a more in depth walk-through of this app, check out <a href="https://www.esri.com/arcgis-blog/products/js-api-arcgis/developers/cim-symbol-builder/">this blog post</a>.</p>
            <p><b>Find a bug?</b> Report it <a href="https://github.com/Esri/cim-symbol-builder-js/issues/new">here</a>.</p>
          </div>
        </CalcitePopover>
        <h1>Preview
            <CalciteIcon id="popover-help" className="helpIcon" icon="question" scale="s" />
        </h1>
        </CalcitePopoverManager>
        <hr></hr>
      </div>
      <SymbolPreview symbolType={currentSymbolType}/>
      <hr></hr>
      {!currentSymbolType ? (
        <div className="symbolTypeBtns" onClick={clickHandler}>
          <span>Select a Symbol Type:</span>
          <button data-type="point">Point</button>
          <button data-type="line">Line</button>
          <button data-type="polygon">Polygon</button>
        </div>
      ) : (
        <SymbolLayersEditor />
      )}
    </div>
  );
};
