import CIMSymbol from "@arcgis/core/symbols/CIMSymbol";
import SimpleRenderer from "@arcgis/core/renderers/SimpleRenderer";
import * as esriLang from "@arcgis/core/core/lang";
import React, { useContext, useState } from "react";
import { SymbolItemContext } from "../contexts/SymbolItemContext";
import { LayersPanel } from "./LayersPanel";
import { SymbolGallery } from "./SymbolGallery";
import { symbolTypeToCIMSymbolType } from "../lib/utils/utils";
import { CalciteSelect, CalciteOption, CalciteLabel, CalciteButton } from "@esri/calcite-components-react";

export const SymbolLayersEditor = () => {
  const {
    symbolJSON,
    inGallery,
    pointLayer,
    lineLayer,
    polygonLayer,
    tempLayers,
    currentSymbolType,
    changeCurrentSymbolType,
    changeInGallery,
    changeSymbolJSON,
  } = useContext(SymbolItemContext);

  const [isJSONView, setIsJSONView] = useState(false);
  const [JSONCopied, setJSONCopied] = useState(false);

  const toggleView = () => {
    setJSONCopied(false);
    setIsJSONView(!isJSONView);
  };

  const handleChangeSymbolType = (evt: CustomEvent) => {
    const element = evt.target as any;
    changeCurrentSymbolType(element.selectedOption.value);
  };

  const updateViewGraphic = () => {
    const cimSymbol = new CIMSymbol({
      data: {
        type: "CIMSymbolReference",
        symbol: symbolJSON,
      },
    });
    switch (currentSymbolType) {
      case "point":
        if (pointLayer) {
          (pointLayer.renderer as SimpleRenderer).symbol = cimSymbol;
        }
        break;
      case "line":
        if (lineLayer) {
          (lineLayer.renderer as SimpleRenderer).symbol = cimSymbol;
        }
        break;
      case "polygon":
        if (polygonLayer) {
          (polygonLayer.renderer as SimpleRenderer).symbol = cimSymbol;
        }
        break;
    }
  };

  const handleCopyJSON = () => {
    navigator.clipboard
      .writeText(JSON.stringify(symbolJSON, null, 2))
      .then(() => {
        setJSONCopied(true);
      });
  };

  const layersCommit = () => {
    changeInGallery(!inGallery);
    const clonedTempLayer = tempLayers.map((layer) => {
      return esriLang.clone(layer);
    });
    changeSymbolJSON({
      type: symbolTypeToCIMSymbolType(currentSymbolType),
      symbolLayers: [...clonedTempLayer, ...symbolJSON.symbolLayers],
    });
  };

  const layersCancel = () => {
    changeInGallery(!inGallery);
    changeSymbolJSON({ ...symbolJSON });
  };

  return (
    <div className="symbolLayersEditor" key="symbolLayersEditor">
      {inGallery
        ? [
            <SymbolGallery key={"gallery"} />,
            <div key={"controlBtns"}>
              <CalciteButton width="half" appearance="outline" key="cancel" onClick={layersCancel}>
                Cancel
              </CalciteButton>
              <CalciteButton width="half" key="ok" onClick={layersCommit}>
                <span>OK</span>
              </CalciteButton>
            </div>,
          ]
        : [
            !isJSONView ? (
              [
                <div className="symbolType" key={"symbolType"}>
                  <CalciteLabel>
                    Symbol Type:
                    <CalciteSelect
                      label="symbolType"
                      onCalciteSelectChange={(e) => handleChangeSymbolType(e)}
                    >
                      <CalciteOption selected={currentSymbolType === "point"} key="point" value="point">
                        Point
                      </CalciteOption>
                      <CalciteOption selected={currentSymbolType === "line"} key="line" value="line">
                        Line
                      </CalciteOption>
                      <CalciteOption selected={currentSymbolType === "polygon"} key="polygon" value="polygon">
                        Polygon
                      </CalciteOption>
                    </CalciteSelect>
                  </CalciteLabel>
                </div>,
                <LayersPanel key="layersPanel"/>,
              ]
            ) : (
              <div key="jsonView" className="JSONView">
                <div className="copyBtn" onClick={handleCopyJSON}>
                  <span>{JSONCopied ? "Copied" : "Copy JSON"}</span>
                </div>
                <pre>{JSON.stringify(symbolJSON, null, 2)}</pre>
              </div>
            ),
            <div key={"buttonGroup"} className="buttonGroup">
              <CalciteButton
                width="half"
                key="addToMapBtn"
                onClick={updateViewGraphic}
              >
                Apply to feature layer
              </CalciteButton>
              <CalciteButton
                width="half"
                appearance="outline"
                key="toggleViewBtn"
                onClick={toggleView}
              >
                {!isJSONView ? "Get Symbol JSON" : "Layers View"}
              </CalciteButton>
            </div>,
          ]}
    </div>
  );
};
