import React, { useEffect, useState, useContext } from "react";
import { PrimitiveGallery } from "./PrimitiveGallery";
import { SymbolItemContext } from "../contexts/SymbolItemContext";
import { isURL } from "../lib/utils/utils";
import { CalciteTabs, CalciteTab, CalciteTabNav, CalciteTabTitle, CalciteButton, CalciteInput, CalciteNotice, CalciteLink, CalciteCard } from "@esri/calcite-components-react";

export const SymbolGallery = () => {
  const { symbolResources, updateSymbolResources } = useContext(
    SymbolItemContext
  );
  const [tabs, setTabs] = useState<JSX.Element[]>();
  const [tabPanels, setTabPanels] = useState<JSX.Element[]>();
  const addWebStyleItemRef = document.getElementById("addWebStyle") as HTMLInputElement;
  const [initializeWebStyleError, setInitializeWebStyleError] = useState<boolean>(false);

  useEffect(() => {
    let currentTabs: JSX.Element[] = [];
    let currentTabPanels: JSX.Element[] = [];

    symbolResources.forEach((resource, index) => {
      currentTabs.push(<CalciteTabTitle key={index}>{resource.name}</CalciteTabTitle>);
      if (resource.resource.type === "primitives") {
        currentTabPanels.push(
          <CalciteTab key={index}>
            <PrimitiveGallery
              url={resource.resource.url}
              allowAddingSymbol={false}
              pgKey={resource.key}
            />
          </CalciteTab>
        );
      } else if (resource.resource.type === "custom") {
        currentTabPanels.push(
          <CalciteTab key={index}>
            <PrimitiveGallery
              cimSymbolItems={resource.resource.items}
              allowAddingSymbol={true}
              pgKey={resource.key}
            />
          </CalciteTab>
        );
      }
    });

    setTabs(currentTabs);
    setTabPanels(currentTabPanels);
  }, [symbolResources]);

  const initializeWebStyleHandler = () => {
    setInitializeWebStyleError(false);
    if (addWebStyleItemRef.value) {
      if (!isURL(addWebStyleItemRef.value)) {
        setInitializeWebStyleError(true);
        return;
      }
      const newSymbolResources = [
        ...symbolResources,
        {
          name: "WebStyle",
          key: "webstyle" + symbolResources.length,
          resource: {
            type: "primitives",
            url: addWebStyleItemRef.value,
          },
        },
      ];
      addWebStyleItemRef.value = "";
      updateSymbolResources(newSymbolResources);
    } else {
      setInitializeWebStyleError(true);
      return;
    }
  };

  return (
    <div className="SymbolGallery" key="symbolGallery">
      <CalciteTabs class="tabs">
        <CalciteTabNav slot="tab-nav">
          {tabs}
          <CalciteTabTitle key="+">+</CalciteTabTitle>
        </CalciteTabNav>
        {tabPanels}
        <CalciteTab key="empty" className="addWebStyle">
          <CalciteCard>
            <span slot="title">Add a web style from URL</span>
            <CalciteLink href="https://www.arcgis.com/sharing/rest/content/items/220936cc6ed342c9937abd8f180e7d1e">Example web style</CalciteLink>
            <CalciteInput id="addWebStyle" type="text" placeholder="https://www.arcgis.com/sharing/rest/content/items/220936cc6ed342c9937abd8f180e7d1e" />
            {initializeWebStyleError ? <CalciteNotice active={initializeWebStyleError} color="red" icon="exclamation-mark-triangle">
                <span slot="message">Invalid URL</span>
              </CalciteNotice> : null}
              <CalciteButton slot="footer-leading" onClick={initializeWebStyleHandler}>Add New Style</CalciteButton>
          </CalciteCard>
        </CalciteTab>
      </CalciteTabs>
    </div>
  );
};
