import React, { useState, useEffect } from "react";
import {
  hexToRgb,
  CIMColorToHex,
  getEffectTemplate,
  getDefaultEffect,
  getMarkerPlacementTemplate,
  getDefaultMarkerPlacement,
} from "../lib/utils/utils";
import useDebounce from "../hooks/useDebounce";
import {
  LineEffectValues,
  PolygonEffectValues,
  LineMarkerPlacementValues,
  PolygonMarkerPlacementValues,
  TextSymbolTemplate,
} from "../data/templates";
import {
  CalciteInput,
  CalciteLabel,
  CalciteSelect,
  CalciteOption,
  CalciteCheckbox,
  CalciteButton,
  CalciteAccordion,
  CalciteAccordionItem
} from "@esri/calcite-components-react"

export const SymbolPropertyItem = (props: any) => {
  const {
    propertyName,
    inputType,
    name,
    value,
    unit,
    onChange,
    keys,
    symbolType,
    layerType,
    values,
  } = props; //textSymbol has keys instead of key for color

  const handleInputChange = (event: CustomEvent) => {
    const target = event.target as HTMLInputElement;
    if (target.type === "number") {
      onChange(propertyName, Number(target.value));
    } else {
      onChange(propertyName, target.value);
    }
  };

  const handleNumberArrayChange = (event: CustomEvent) => {
    const target = event.target as HTMLInputElement;
    const value = target.value;
    const valueToArray = value.split(",");
    onChange(propertyName, valueToArray);
  };

  const handleCheckboxChange = (event: CustomEvent) => {
    onChange(propertyName, (event.target as HTMLInputElement).checked);
  };

  const handleSelectChange = (event: CustomEvent) => {
    const target = event.target as any;
    onChange(propertyName, target.selectedOption.value)
  }

  const handleCustomInputChange = (newValue: any) => {
    if (propertyName) {
      onChange(propertyName, newValue);
    } else if (keys) {
      //text symbol color
      const clonedValue = { ...value };
      let pointer = clonedValue;
      for (let i = 1; i < keys.length - 1; i++) {
        pointer = pointer[keys[i]];
      }
      pointer[keys[keys.length - 1]] = newValue;
      onChange(keys[0], clonedValue);
    }
  };

  const getValue = (value: any, keys: any) => {
    let pointer = value;
    for (let i = 1; i < keys.length; i++) {
      pointer = pointer[keys[i]];
    }

    return pointer;
  };

  switch (inputType) {
    case "number":
      return (
        <div className="symbolPropertyItem">
          <CalciteLabel>{name}
              <CalciteInput onCalciteInputInput={(e) => handleInputChange(e)} value={value} type="number" step={0.001} suffixText={unit} />
          </CalciteLabel>
        </div>
      );
    case "numbers":
      return (
        <div className="symbolPropertyItem">
          <CalciteLabel>{name}
            <CalciteInput onCalciteInputInput={(e) => handleNumberArrayChange(e)} type="text" value={value} />
          </CalciteLabel>
        </div>
      );
    case "text":
      return (
        <div className="symbolPropertyItem">
          <CalciteLabel>{name}
            <CalciteInput onCalciteInputInput={(e) => handleInputChange(e)} type="text" value={value} />
          </CalciteLabel>
        </div>
      );
    case "texts":
      return (
        <div className="symbolPropertyItem">
          <span className="selectLabel">{name}</span>
          <CalciteSelect label={name} onCalciteSelectChange={(e) => handleSelectChange(e)}>
            {values.map((v: string) => (
              <CalciteOption selected={v===value} key={v} value={v}>
                {v}
              </CalciteOption>
            ))}
          </CalciteSelect>
        </div>
      );
    case "imgURI":
      return (
        <div className="symbolPropertyItem">
          <CalciteLabel>{name}
            <CalciteInput type="textarea" onCalciteInputInput={(e) => handleInputChange(e)} value={value} />
          </CalciteLabel>
        </div>
      );
    case "boolean":
      return (
        <div className="symbolPropertyItem">
          <CalciteCheckbox
            onCalciteCheckboxChange={(e) => handleCheckboxChange(e)}
            checked={value}
          />
          <span className="checkBoxLabel">{name}</span>
        </div>
      );
    case "Color":
      return (
        <div className="symbolPropertyItem">
          {/* <span className="inputLabel">{name}</span> */}
          <ColorInput
            handleInputChange={handleCustomInputChange}
            value={keys ? getValue(value, keys) : value}
          />
        </div>
      );
    case "Point":
      return (
        <div className="symbolPropertyItem">
          <CalciteLabel>{name}
            <PointInput
              handleInputChange={handleCustomInputChange}
              value={value}
            />
          </CalciteLabel>
        </div>
      );
    case "AnchorPoint":
      return (
        <div className="symbolPropertyItem">
          <CalciteLabel>{name}
            <PointInput
              handleInputChange={handleCustomInputChange}
              value={value}
              unit={"%"}
            />
          </CalciteLabel>
        </div>
      );
    case "TextSymbol":
      return (
        <div className="symbolPropertyItem">
          {/* <span className="inputLabel">{name}</span> */}
          <TextSymbolInput
            handleInputChange={handleCustomInputChange}
            value={value}
          />
        </div>
      );
    case "MarkerPlacement":
      if (symbolType === "line" || symbolType === "polygon") {
        return (
          <div className="symbolPropertyItem">
            <MarkerPlacementInput
              symbolType={symbolType}
              values={
                symbolType === "line"
                  ? LineMarkerPlacementValues
                  : PolygonMarkerPlacementValues
              }
              handleInputChange={handleCustomInputChange}
              value={value}
            />
          </div>
        );
      }
      return null;
    case "Effects":
      return (
        <div className="symbolPropertyItem">
          <span className="selectLabel">{name}</span>
          <EffectsInput
            handleInputChange={handleCustomInputChange}
            layerType={layerType}
              values={
                layerType.includes("Stroke")
                  ? LineEffectValues
                  : PolygonEffectValues
              }
            value={value}
          />
        </div>
      )
    default:
      return (
        <div className="symbolPropertyItem">
          <CalciteLabel>{name}</CalciteLabel>
        </div>
      );
  }
};

const EffectsInput = (props: any) => {
  let addEffect = props.values[0];
  const handleEffectsChange = (property: string, newValue: any) => {
    if (property === "type") {
      // add new effect
      const newEffect = getDefaultEffect(newValue);
      let cloneProps = props.value;
      if (cloneProps){
        cloneProps.push(newEffect);
      } else {
        // handle case where no effects are are added yet
        cloneProps = [newEffect];
      }
      props.handleInputChange(cloneProps);
    } 
    else if (property === "remove") {
      // remove effect at index
      let index = newValue;
      let cloneProps = props.value;
      cloneProps.splice(index, 1);
      props.handleInputChange(cloneProps);
    } else {
      // handle property change
      let index = property[property.length-1];
      let updatedProp = property.slice(0,-1);
      const updatedEffect = props.value[index];
      let cloneProps = props.value;
      updatedEffect[updatedProp] = newValue;
      cloneProps[index] = updatedEffect;
      props.handleInputChange(cloneProps);
    }
  };

  const updateSelectedValue = (event: any) => {
    addEffect = event.target.selectedOption.value;
  }
  
  if (!props.value){
    return (
      <>
        <CalciteSelect key="addEffect" label="addEffect" onCalciteSelectChange={(e) => updateSelectedValue(e)}>
          {props.values.map((value: string, index: number) => {
            return (
              <CalciteOption selected={addEffect === value} key={index} value={value}>
                {value}
              </CalciteOption>
            );
          })}
        </CalciteSelect>
        <CalciteButton
          appearance="outline"
          scale="s"
          onClick={() => { handleEffectsChange("type", addEffect) }}
        >Add effect</CalciteButton>
      </>
    )
  }

  return (
    <>
      <CalciteAccordion>
      {props.value.map((effect: any, i: number) => {
          return (
            <>
              <CalciteAccordionItem itemTitle={effect.type} key={effect.type+i}>
              {getEffectTemplate(effect.type).map(
                (input: any, index: number) => {
                  const value =
                    effect[input.key] != null
                      ? effect[input.key]
                      : input.default;
                  return (
                    <SymbolPropertyItem
                      key={input.key + i + "_" + index}
                      value={value}
                      propertyName={input.key+i}
                      keys={input.keys}
                      inputType={input.type}
                      name={input.name}
                      unit={input.unit}
                      values={input.values}
                      onChange={handleEffectsChange}
                    />
                  );
                }
              )}
              <CalciteButton key={"remove"+i} appearance="outline" color="red" scale="s" onClick={(e) => handleEffectsChange("remove", i)}>Delete Effect</CalciteButton>
              </CalciteAccordionItem>
            </>
          )
      })}
      </CalciteAccordion>
      <CalciteSelect label="addEffect2" key="addEffect2" onCalciteSelectChange={(e) => updateSelectedValue(e)}>
          {props.values.map((value: string, index: number) => {
            return (
              <CalciteOption selected={addEffect === value} key={"addEffect2" + index} value={value}>
                {value}
              </CalciteOption>
            );
          })}
        </CalciteSelect>
      <CalciteButton
        appearance="outline"
        scale="s"
        onClick={() => { handleEffectsChange("type", addEffect) }}
      >Add effect</CalciteButton>
    </>
  )
}

const MarkerPlacementInput = (props: any) => {
  const handleMarkerPlacementChange = (property: string, newValue: any) => {
    if (property === "type") {
      const newMP = getDefaultMarkerPlacement(newValue.selectedOption.value);
      props.handleInputChange(newMP);
    } else {
      const newMP = { ...props.value };
      newMP[property] = newValue;
      props.handleInputChange(newMP);
    }
  };

  return (
    <>
      <div className="symbolPropertyItem">
        <span className="selectLabel">MarkerPlacement</span>
        <CalciteSelect
          label="markerPlacementSelect"
          onCalciteSelectChange={(e) => {
            handleMarkerPlacementChange("type", e.target);
          }}
        >
          {props.values.map((value: string, index: number) => {
            return (
              <CalciteOption selected={props.value.type === value} key={index} value={value}>
                {value}
              </CalciteOption>
            );
          })}
        </CalciteSelect>
      </div>
      {getMarkerPlacementTemplate(props.value.type).map(
        (input: any, index: number) => {
          const value =
            props.value[input.key] != null
              ? props.value[input.key]
              : input.default;
          return (
            <SymbolPropertyItem
              key={"mp" + index}
              value={value}
              propertyName={input.key}
              keys={input.keys}
              inputType={input.type}
              name={input.name}
              unit={input.unit}
              values={input.values}
              onChange={handleMarkerPlacementChange}
            />
          );
        }
      )}
    </>
  );
};

const ColorInput = (props: any) => {
  const [color, setColor] = useState(
    props.value ? CIMColorToHex(props.value) : "#000000"
  );
  const [opacity, setOpacity] = useState(props.value ? props.value[3] : 255);
  const debouncedColor = useDebounce(color, 16);
  const debouncedOpacity = useDebounce(opacity, 16);

  const handleInputChange = props.handleInputChange;

  useEffect(() => {
    if (debouncedColor) {
      const rgb = hexToRgb(debouncedColor);
      const cimColor = [rgb?.r, rgb?.g, rgb?.b, debouncedOpacity];
      handleInputChange(cimColor);
    }
  }, [debouncedColor, debouncedOpacity]);

  const handleColorInputChange = (event: CustomEvent) => {
    const inputValue = (event.target as HTMLInputElement).value;
    setColor(inputValue);
  };

  const handleOpacityInputChange = (event: CustomEvent) => {
    const inputValue = Number((event.target as HTMLInputElement).value);
    setOpacity(inputValue);
  };

  return (
    <div className="colorInputContainer">
      <div>
        <CalciteLabel>Color
          <CalciteInput type="color" value={color} onCalciteInputInput={(e) => handleColorInputChange(e)} />
        </CalciteLabel>
      </div>
      <div>
        <CalciteLabel>Opacity
          <CalciteInput
            type="number"
            min={0}
            max={255}
            value={opacity}
            onCalciteInputInput={(e) => handleOpacityInputChange(e)}
          ></CalciteInput>
        </CalciteLabel>
      </div>
    </div>
  );
};

const PointInput = (props: any) => {
  const value = props.value;
  const unit = props.unit;
  const handleInputChange = props.handleInputChange;

  const handleXInputChange = (event: CustomEvent) => {
    let newX = Number((event.target as HTMLInputElement).value);
    if (unit === "%") {
      newX /= 100;
    }
    const newValue = {
      ...value,
      x: newX,
    };
    handleInputChange(newValue);
  };

  const handleYInputChange = (event: CustomEvent) => {
    let newY = Number((event.target as HTMLInputElement).value);
    if (unit === "%") {
      newY /= 100;
    }
    const newValue = {
      ...value,
      y: newY,
    };
    handleInputChange(newValue);
  };

  return (
    <div className="pointInputContainer">
      <div>
        <CalciteLabel>x
            <CalciteInput
              type="number"
              numberButtonType= "none"
              value={unit === "%" ? value?.x * 100 : value?.x}
              onCalciteInputInput={(e) => handleXInputChange(e)}
              suffixText={props.unit}
            ></CalciteInput>
        </CalciteLabel>
      </div>
      <div>
        <CalciteLabel>y
            <CalciteInput
              type="number"
              numberButtonType= "none"
              value={unit === "%" ? value?.y * 100 : value?.y}
              onCalciteInputInput={(e) => handleYInputChange(e)}
              suffixText={props.unit}
            ></CalciteInput>
        </CalciteLabel>
      </div>
    </div>
  );
};

const TextSymbolInput = (props: any) => {
  const template = TextSymbolTemplate;
  const textSymbolValue = props.value;
  const handleInputChange = props.handleInputChange;

  const handleTextSymbolInputChange = (property: string, newValue: any) => {
    const newSymbol = { ...textSymbolValue };
    newSymbol[property] = newValue;
    handleInputChange(newSymbol);
  };

  return (
    <div className="textInputContainer">
      {template && textSymbolValue
        ? template.map((input: any, index: number) => {
            const value =
              textSymbolValue[input.key] != null
                ? textSymbolValue[input.key]
                : input.keys
                ? textSymbolValue[input.keys[0]]
                : input.default;
            return (
              <SymbolPropertyItem
                key={index}
                value={value}
                propertyName={input.key}
                keys={input.keys}
                inputType={input.type}
                name={input.name}
                unit={input.unit}
                onChange={handleTextSymbolInputChange}
              />
            );
          })
        : null}
    </div>
  );
};
